import React, { Component } from 'react';
import '../../node_modules/react-vis/dist/style.css';
import { XYPlot, YAxis, HorizontalGridLines, LineSeries } from 'react-vis';
import './Graph.css';

class Graph extends Component {

    getConfirmedData = () => {
        let a = {};
        let confirmedArr = [];
        for (let i = 30; i < this.props.data.length; i++) {
            a = {};
            a.x = i;
            a.y = parseInt(this.props.data[i].dailyconfirmed);
            confirmedArr.push(a);
        }
        return confirmedArr;
    }

    getRecoveredData = () => {
        let b = {};
        let recoveredArr = [];
        for (let i = 30; i < this.props.data.length; i++) {
            b = {};
            b.x = i;
            b.y = parseInt(this.props.data[i].dailyrecovered);
            recoveredArr.push(b);
        }
        return recoveredArr;
    }

    getDeathsData = () => {
        let c = {};
        let deathsArr = [];
        for (let i = 30; i < this.props.data.length; i++) {
            c = {};
            c.x = i;
            c.y = parseInt(this.props.data[i].dailydeceased);
            deathsArr.push(c);
        }
        return deathsArr;
    }

    render() {
        const confirmed = this.getConfirmedData();
        const recovered = this.getRecoveredData();
        const deaths = this.getDeathsData();
        return (
            <div>
                <div>
                    <h4 className="confirmed">Confirmed:</h4>
                    <div className="Graph">
                        <XYPlot height={200} width={300}>
                            <YAxis />
                            <HorizontalGridLines />
                            <LineSeries data={confirmed} color="red" />
                        </XYPlot>
                    </div>
                </div>
                <div>
                    <h4 className="recovered">Recovered:</h4>
                    <div className="Graph">
                        <XYPlot height={200} width={300}>
                            <YAxis />
                            <HorizontalGridLines />
                            <LineSeries data={recovered} color="green" />
                        </XYPlot>
                    </div>
                </div>
                <div>
                    <h4 className="deaths">Deaths:</h4>
                    <div className="Graph">
                        <XYPlot height={200} width={300}>
                            <YAxis />
                            <HorizontalGridLines />
                            <LineSeries data={deaths} color="#a0a3a7" />
                        </XYPlot>
                    </div>

                </div>
            </div>
        );
    }
}

export default Graph;