import React from "react";
import India from "@svg-maps/india";
import { SVGMap } from "react-svg-map";
import "../../node_modules/react-svg-map/src/svg-map.scss";
import "./Map.css";

class Map extends React.Component {

    state = {
        state: "Click on the Map",
        confirmed: 0,
        recovered: 0,
        deaths: 0,
        active: 0,
        deltaConfirmed: 0,
        deltaRecovered: 0,
        deltaDeaths: 0
    }

    locationMouseOver = (e) => {
        for (let i = 0; i < this.props.data.length; i++) {
            if (this.props.data[i].statecode === e.currentTarget.id.toUpperCase()) {
                this.setState({
                    state: this.props.data[i].state,
                    confirmed: this.props.data[i].confirmed,
                    recovered: this.props.data[i].recovered,
                    deaths: this.props.data[i].deaths,
                    active: this.props.data[i].active,
                    deltaConfirmed: this.props.data[i].deltaconfirmed,
                    deltaRecovered: this.props.data[i].deltarecovered,
                    deltaDeaths: this.props.data[i].deltadeaths
                })
            }
        }
        // document.getElementById("mapStateData").style.visibility = "visible";
    }

    render() {
        return (
            <div className="map-style">
                <div className="state-data">
                    <h4 className="state-data-heading">{this.state.state}</h4>
                    <div className="row">
                        <div className="col-sm-6 col-6 confirmed-data">
                            <p className="cases">CONFIRMED</p>
                            <p>{this.state.confirmed} [+{this.state.deltaConfirmed}]</p>
                        </div>
                        <div className="col-sm-6 col-6 active-data">
                            <p className="cases">ACTIVE</p>
                            <p>{this.state.active}</p>
                        </div>
                        <div className="col-sm-6 col-6 recovered-data">
                            <p className="cases">RECOVERED</p>
                            <p>{this.state.recovered} [+{this.state.deltaRecovered}]</p>
                        </div>
                        <div className="col-sm-6 col-6 deaths-data">
                            <p className="cases">DEATHS</p>
                            <p>{this.state.deaths} [+{this.state.deltaDeaths}]</p>
                        </div>
                    </div>
                </div>
                <SVGMap
                    className="svg-map"
                    locationClassName="svg-map__location"
                    map={India}
                    onLocationMouseOver={this.locationMouseOver}
                />
            </div>
        )
    }
}

export default Map;